import React from 'react'
import cx from 'classnames'
import { Layout, Menu, Select, Drawer } from 'antd'
import { MenuProps } from 'antd/lib/menu'
import {
  SettingOutlined,
  HomeOutlined,
  CaretDownOutlined,
  MenuOutlined,
  LogoutOutlined,
  UserOutlined,
} from '@ant-design/icons'
import styled from 'styled-components'
import Breadcrumb from '@/components/breadcrumb'
import LogoutConfirmModal from '@/components/logout-confirm-modal'
import { useBreadcrumbState, useHeaderContentValue } from '@/hooks/useApp'
import useAuth from '@/hooks/useAuth'
import useVisible from '@/hooks/useVisible'
import history from '@/utils/history'
import useMobile from '@/hooks/useMobile';
import { envStore } from '@/constants/env'
import * as routes from '@/constants/routes'
import { Divisions } from '@/constants/enums'
import { useUser } from '@/data/useUser'
import { User } from '@/constants/types'

export default function AppHeader() {
  const [items] = useBreadcrumbState()
  const content = useHeaderContentValue()
  const setUser = useUser()

  const { logout, user } = useAuth()
  const isMobile = useMobile()

  const [current, setCurrentPath] = React.useState(history.location.pathname)

  const [isOpen, setIsOpen] = useVisible()
  const [logoutModalVisible, handleLogoutModalVisible] = useVisible()

  const handleLogoClick = () => {
    if (user.isAdmin) {
      setUser((prev: any) => ({ ...prev, viewDivision: false }))
    }
    history.push('/')
  }

  const handleTo = (pathname?: string) => () => {
    if (pathname) {
      setCurrentPath(pathname)
      history.push(pathname)
    }
  }

  const getMenuClassName = (targetUrl: string) => {
    return cx({
      'is--currentt': current !== '/' && targetUrl.includes(current),
    })
  }

  const handleUserChange = (value: string) => {
    const isClinician = value === Divisions.CLINICIAN

    setUser(
      (prev: User | null) =>
        ({
          ...prev,
          isClinician,
          division: isClinician ? prev?.division : value,
        } as any)
    )

    if (isClinician) {
      history.push('/')
    }
  }

  const handleRoleChange = (value: number) => {
    setUser(
      (prev: User | null) =>
        ({
          ...prev,
          isAdmin: !!value,
          viewDivision: false,
        } as any)
    )
    history.push('/')
  }

  const handleLogout = () => {
    logout({
      onCompleted: () => {
        window.location.href = envStore.oauthUrl
      },
    })
  }

  const renderMenu = ({ mode = 'horizontal' }: MenuProps = {}) => {
    let Item = isMobile ? Menu.Item : MenuItem
    let SubItem = isMobile ? Menu.SubMenu : SubMenu

    if (!showMenu) {
      return (
        <Menu
          mode={mode}
          className="bg-transparent ml-3"
          style={{ borderBottom: 'none', lineHeight: '46px' }}>
          <Item
            key={routes.REPORT_LIST}
            onClick={handleTo(routes.REPORT_LIST)}
            className={getMenuClassName(routes.REPORT_LIST)}>
            <div className="flex items-center text-base">
              <SettingOutlined />
              
            </div>
          </Item>
        </Menu>
      )
    }

    return (
      <Menu
        mode={mode}
        className="bg-transparent ml-3"
        style={{
          borderBottom: 'none',
          lineHeight: '46px',
          borderRight: 'none',
        }}>
        {user.division === Divisions.PATH && (
          <Item
            key={routes.IMAGE_SCHEDULER}
            onClick={handleTo(routes.IMAGE_SCHEDULER)}
            className={getMenuClassName(routes.IMAGE_SCHEDULER)}>
            <div className="flex items-center text-base">
              <SettingOutlined />
              
            </div>
          </Item>
        )}
        {user.division === Divisions.RAD && (
          <Item
            key={routes.REPORT_LIST}
            onClick={handleTo(routes.REPORT_LIST)}
            className={getMenuClassName(routes.REPORT_LIST)}>
            <div className="flex items-center text-base">
              <SettingOutlined />
              
            </div>
          </Item>
        )}
        {user.division === Divisions.PATH && (
          <SubItem
            key={routes.REPORT_LIST}
            className={getMenuClassName(routes.REPORT_LIST)}
            popupOffset={[-14, 0]}
            popupClassName="rounded"
            title={
              <div className="flex items-center text-base">
                <SettingOutlined />
                
                {!isMobile && <CaretDownOutlined className="ml-2 mr-0" />}
              </div>
            }>
            <Menu.Item
              key={routes.REPORT_LIST}
              onClick={handleTo(routes.REPORT_LIST)}
              className={getMenuClassName(routes.REPORT_LIST)}>
              <div className="flex items-center text-sm">
                <SettingOutlined />
                
              </div>
            </Menu.Item>
            <Menu.Item
              key={routes.REPORT_VERSION_COMPARISON}
              onClick={handleTo(routes.REPORT_VERSION_COMPARISON)}
              className={getMenuClassName(routes.REPORT_VERSION_COMPARISON)}>
              <div className="flex items-center text-sm">
                <SettingOutlined />
                
              </div>
            </Menu.Item>
            <Menu.Item
              key={routes.YELLOW_CARD_INQUIRY}
              onClick={handleTo(routes.YELLOW_CARD_INQUIRY)}
              className={getMenuClassName(routes.YELLOW_CARD_INQUIRY)}>
              <div className="flex items-center text-sm">
                <SettingOutlined />
                
              </div>
            </Menu.Item>
            <Menu.Item
              key={routes.BLOOD_BANK_REVIEW}
              onClick={handleTo(routes.BLOOD_BANK_REVIEW)}
              className={getMenuClassName(routes.BLOOD_BANK_REVIEW)}
              style={{ display: 'none' }}>
              <div className="flex items-center text-sm">
                <SettingOutlined />
                
              </div>
            </Menu.Item>
          </SubItem>
        )}
        <Item
          key={routes.REPORT_INQUIRY}
          onClick={handleTo(routes.REPORT_INQUIRY)}
          className={getMenuClassName(routes.REPORT_INQUIRY)}>
          <div className="flex items-center text-base">
            <SettingOutlined />
            
          </div>
        </Item>
        <SubItem
          key={routes.REPORT_TEMPLATE_LIST}
          className={getMenuClassName(routes.REPORT_TEMPLATE_LIST)}
          popupOffset={[-14, 0]}
          popupClassName="rounded"
          title={
            <div className="flex items-center text-base">
              <SettingOutlined />
              
              {!isMobile && <CaretDownOutlined className="ml-2 mr-0" />}
            </div>
          }>
          <Menu.Item
            key={routes.REPORT_TEMPLATE_LIST}
            onClick={handleTo(routes.REPORT_TEMPLATE_LIST)}
            className={getMenuClassName(routes.REPORT_TEMPLATE_LIST)}>
            <div className="flex items-center text-sm">
              <SettingOutlined />
              
            </div>
          </Menu.Item>
          <Menu.Item
            key={routes.PHRASE_LIST}
            onClick={handleTo(routes.PHRASE_LIST)}
            className={getMenuClassName(routes.PHRASE_LIST)}>
            <div className="flex items-center text-sm">
              <SettingOutlined />
              
            </div>
          </Menu.Item>
        </SubItem>
        <Item
          key={routes.IMAGE_VIEWER_TUTORIAL}
          onClick={handleTo(routes.IMAGE_VIEWER_TUTORIAL)}
          className={getMenuClassName('outside')}>
          <div className="flex items-center text-base">
            <SettingOutlined />
            
          </div>
        </Item>
      </Menu>
    )
  }

  const showMenu = !user.isClinician

  return (
    <>
      {logoutModalVisible && (
        <LogoutConfirmModal
          onOk={handleLogout as any}
          onCancel={handleLogoutModalVisible}
        />
      )}
      {isOpen && (
        <Drawer
          visible
          placement="left"
          bodyStyle={{ paddingLeft: 0 }}
          closable={false}
          onClose={setIsOpen}>
          {renderMenu({ mode: 'vertical' })}
        </Drawer>
      )}
      <Header>
        <div className="flex items-center justify-between">
          {isMobile && (
            <>
              <MenuOutlined
                className="text-white text-2xl"
                onClick={setIsOpen}
              />
              <Logo onClick={handleLogoClick}>
                <img
                  src={`${envStore.publicUrl}/images/logo-sm@2x.png`}
                  alt="app-logo"
                  className="cursor-pointer"
                />
              </Logo>
              <div className="flex items-center text-white">
                <div>Hi, {user.name}</div>
                <LogoutOutlined
                  className="text-white text-base ml-2"
                  onClick={handleLogoutModalVisible}
                  style={{ marginRight: 0 }}
                />
              </div>
            </>
          )}
          {!isMobile && (
            <>
              <div className="flex items-center">
                <Logo onClick={handleLogoClick}>
                  <img
                    src={`${envStore.publicUrl}/images/logo-sm@2x.png`}
                    alt="app-logo"
                    className="cursor-pointer"
                  />
                </Logo>
                {renderMenu()}
              </div>
              <div className="flex items-center">
                {!envStore.useOAuth && (
                  <>
                    <Select
                      bordered={false}
                      placeholder=""
                      className="text-white"
                      style={{ width: 96, marginRight: -30 }}
                      suffixIcon={false}
                      defaultValue={user.isAdmin ? 1 : 0}
                      onChange={handleRoleChange}>
                      <Select.Option value={1}></Select.Option>
                      <Select.Option value={0}></Select.Option>
                    </Select>
                    <Select
                      bordered={false}
                      placeholder=""
                      className="text-white"
                      style={{ width: 100, marginRight: -20 }}
                      suffixIcon={false}
                      defaultValue={user.division}
                      onChange={handleUserChange}>
                      <Select.Option value={Divisions.RAD}>
                        
                      </Select.Option>
                      <Select.Option value={Divisions.PATH}>
                        
                      </Select.Option>
                      <Select.Option value={Divisions.CLINICIAN}>
                        
                      </Select.Option>
                    </Select>
                  </>
                )}
                <div className="flex items-center text-white">
                  <div className="mr-2">Hi, {user.name}</div>
                  <UserOutlined
                    className="text-white text-xl"
                    style={{ marginRight: 0 }}
                    onClick={handleLogoutModalVisible}
                  />
                </div>
              </div>
            </>
          )}
        </div>
        <div className="mt-3 ml-4 pb-0 flex justify-between">
          <Breadcrumb
            separator=">"
            style={{ minHeight: 32, display: 'flex', alignItems: 'center' }}>
            <Breadcrumb.Item onClick={handleTo('/')}>
              <HomeOutlined className="relative" style={{ top: -4 }} />
            </Breadcrumb.Item>
            {items.map((x, idx) => (
              <Breadcrumb.Item
                key={`breadcrumb-${idx}`}
                onClick={handleTo(x.href)}>
                {x.name}
              </Breadcrumb.Item>
            ))}
          </Breadcrumb>
          {content}
        </div>
      </Header>
    </>
  )
}

const Header = styled(Layout.Header)`
  &.ant-layout-header {
    height: auto;
    padding: 16px 1.7rem 10px 2.1rem;
    background: ${p => `linear-gradient(-90deg, #42c2b8, ${p.theme.primary})`};
    color: ${p => p.theme.primary};
    box-shadow: 0 0 20px ${p => p.theme.purple150};
  }

  .anticon {
    cursor: pointer;
  }
`

const MenuItem = styled(Menu.Item)`
  &.ant-menu-item {
    margin: 0 2px !important;
    padding: 0 10px !important;
    border-radius: 6px;
    border-bottom: none !important;
    color: #fff;

    &.is--current,
    &:hover {
      color: #fff !important;
      background: rgba(24, 144, 138, 1) !important;
    }
  }

  &.ant-menu-item-selected {
    color: #fff !important;
  }
`

const SubMenu = styled(Menu.SubMenu)`
  &.ant-menu-submenu {
    margin: 0 5px !important;
    padding: 0 15px !important;
    border-radius: 6px;
    border-bottom: none !important;

    .ant-menu-submenu-title {
      color: #fff;
    }

    &.is--current,
    &:hover {
      background: rgba(24, 144, 138, 1) !important;
    }
  }
`

const Logo = styled.div``
