function App() {
  const { isAuthenticated, setUser, setIsAuthenticated } = useAuth()

  const routes = useRoutes()

  const renderRouteItem = React.useCallback((item: RouteItem) => {
    const path = `${item.path}${item.param ? `/${item.param}` : ''}`

    return (
      <Route
        exact
        key={item.id}
        path={path}
        component={
          item.component ? item.component : () => <h1> {item.title} </h1>
        }
      />
    )
  }, [])

  const renderRoute: any = React.useCallback(
    (data: RouteItem[]) => {
      return data.map((item: RouteItem) => {
        return [
          renderRouteItem(item),
          item.children && renderRoute(item.children),
          item.hide && renderRoute(item.hide),
        ]
      })
    },
    [renderRouteItem]
  )

  const user = queryString(history.location.search)

  React.useEffect(() => {
    if (user.userid) {
      const loginUser = {
        ...user,
        id: user.userid,
        name: user.user,
        isClinician: user.division === 'guest',
        isAdmin: user.division === 'MANAGER',
        viewDivision: false,
        expiredAt: new Date(),
      } as User

      setIsAuthenticated(true)
      setUser(loginUser)

      Store.set('vghks_user', loginUser)
      history.push('/')
    }
  }, [JSON.stringify(user)]) // eslint-disable-line

  return (
    <ErrorBoundary>
      <GlobalStyles />
      {!isAuthenticated && (
        <div className="flex items-center justify-center">
          <Loading className="mr-2" />
          <div className="relative" css={{ top: 3 }}>
            ...
          </div>
        </div>
      )}
      {isAuthenticated && (
        <Layout>
          <AppHeader />
          <React.Suspense fallback={<Loading />}>
            <Switch>
              <Route exact path="/" component={Home} />
              {renderRoute(routes)}
              <Route component={Exception} />
            </Switch>
          </React.Suspense>
        </Layout>
      )}
    </ErrorBoundary>
  )
}

export default App
