import React from 'react'
import cx from 'classnames'
import { Form } from 'antd'
import { pathOr } from 'ramda'
import Radio from '@/components/radio'
import Input from '@/components/input'
import {
  SnippetTemplateSelectionModal,
  Setting,
  Option,
  defaultValue,
} from '@/components/snippet-template-selection-modal'
import { mapIndexed } from '@/utils/webHelper'
import { ReportTemplateProps } from '@/constants/types'
import { useUpdateSnippet } from '@/services/report-template'
const { compose } = require('ramda')

function SnippetTemplateSingleSelection(props: ReportTemplateProps) {
  const { item, editModalVisible, handleEditModalVisible, onUpdate } = props

  const { setting, handleOk } = useUpdateSnippet<Setting>({
    item,
    onUpdate,
    defaultValue,
  })

  return (
    <>
      {editModalVisible && (
        <SnippetTemplateSelectionModal
          item={setting}
          onCancel={handleEditModalVisible}
          onOk={handleOk}
        />
      )}
      <Form.Item noStyle name={['data', item.id]}>
        <Radio.Group>
          {compose(
            mapIndexed((x: Option, idx: number) => (
              <Radio
                key={x.id}
                className={cx('block', { 'mt-1': idx > 0 })}
                defaultChecked={false}
                value={x.text}>
                {x.text || ''}
                {x.freetext && (
                  <Input
                    size="small"
                    bordered={false}
                    placeholder=""
                    className="ml-3 w-full"
                  />
                )}
              </Radio>
            )),
            pathOr([], ['options'])
          )(setting)}
        </Radio.Group>
      </Form.Item>
    </>
  )
}

export default SnippetTemplateSingleSelection
